import React from "react";
import "./styles.css";

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      country: 0,
      timezone: 0,
      population: 0,
      temperature: 0,
      lat: 0,
      long: 0,
      wind: 0,
      humidity: 0,
      feelsLike: 0,
      city: "Bangalore",
      weatherType: ""
    };
  }

  componentDidMount() {
    this.getData("Delhi");
  }

  getData = (value) => {
    fetch(
      "https://api.openweathermap.org/data/2.5/forecast?q=" +
        value +
        "&appid=a1fb5f7987aba68ff2dbce87889aee36"
    )
      .then((response) => response.json())
      .then((json) => {
        console.log(json);
        this.setState({
          temperature: json.list[0].main.temp,
          timezone: json.city.timezone,
          humidity: json.list[0].main.humidity,
          feelsLike: json.list[0].main.feels_like,
          lat: json.city.coord.lat,
          long: json.city.coord.lon,
          population: json.city.population,
          wind: json.list[0].wind.speed,
          country: json.city.country,
          city: value,
          weatherType: json.list[0].weather[0].main
        });
      })
      .catch((error) => {
        console.error(error);
      });
  };

  render() {
    return (
      <>
        <div>
          <h1>WEATHER APP</h1>
          <div
            className="city"
            onClick={() => {
              this.getData("Bangalore");
            }}
          >
            Bangalore
          </div>
          <div
            className="city"
            onClick={() => {
              this.getData("Delhi");
            }}
          >
            Delhi
          </div>
          <div
            className="city"
            onClick={() => {
              this.getData("Mumbai");
            }}
          >
            Mumbai
          </div>

          <div className="container">
            {/* WEATHER COMPONENT */}
            <div className="weather">
              <div className="container-inner">
                {/* LEFT SIDE CONTENT */}
                <div className="content-inner left-side">
                  <span className="primary">{this.state.city}</span>
                  <br />

                  <span className="secondary">
                    as of {new Date().toString()}
                  </span>
                  <br />
                  <br />

                  <span className="temp">{this.state.temperature}</span>
                  <br />
                  <br />

                  <span className="primary">{this.state.weatherType}</span>
                  <br />
                  <span className="secondary">42% chance of rain</span>
                </div>
                {/* RIGHT SIDE CONTENT */}
                <div className="content-inner right-side">
                  <span className="primary">{this.state.humidity}</span>
                  <br />
                  <span className="secondary">Humidity</span>
                  <br />
                  <br />

                  <span className="primary">{this.state.feelsLike}</span>
                  <br />
                  <span className="secondary">Feels like</span>
                  <br />
                  <br />

                  <span className="primary">{this.state.wind}</span>
                  <br />
                  <span className="secondary">wind</span>
                  <br />
                  <br />
                </div>
              </div>
            </div>

            {/* DETAILS COMPONENT */}
            <div className="details">
              <div className="container-inner right">
                {/* LEFT SIDE CONTENT */}
                <div className="content-inner">
                  <br />
                  <br />
                  <span className="primary">
                    {this.state.lat} , {this.state.long}
                  </span>
                  <br />
                  <span className="secondary">Lat and Long</span>
                  <br />
                  <br />
                  <br />
                  <br />
                  <br />
                  <br />

                  <span className="primary">{this.state.timezone}</span>
                  <br />
                  <span className="secondary">Time Zone</span>
                  <br />
                  <br />
                </div>

                {/* RIGHT SIDE CONTENT */}
                <br />
                <div className="content-inner right-side">
                  <br />
                  <br />

                  <span className="primary">{this.state.population}</span>
                  <br />
                  <span className="secondary">Population</span>
                  <br />
                  <br />
                  <br />
                  <br />
                  <br />

                  <span className="primary">{this.state.country}</span>
                  <br />
                  <span className="secondary">country</span>
                  <br />
                  <br />
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
